	export default {
		data() {
			return {
				qian:null,
				wx:false,
				flag:false,
				img:null,
				upData:null
			}
		},
		onLoad() {
		   
		},
		methods: {
			tixian:function(){
				if(this.qian>0){
					this.flag=!this.flag;
				}else{
					uni.showToast({
						title:'请正确输入提现金额',
						icon:'none'
					})
				}
			},
			xiayibu:function(){
				let that=this;
				uni.showLoading({
					title:'发起提现中..',mask: true
				})
				uni.request({
						url: that.url1+"/dinghai/user/WithdrawalsMoney",
						header:{
							"Content-Type" : "application/x-www-form-urlencoded"
						},
						method:'POST',
						data: {
							uid:that.user.uid,
							money:that.qian
						},
				    success: (res) => {
						console.log(res)
						if(res.data.flag==200){
							uni.request({
									url: that.url1+"/dinghai/user/insertWithdrawals",
									header:{
										"Content-Type" : "application/x-www-form-urlencoded"
									},
									method:'POST',
									data: {
										withdrawalsmoney:that.qian,
										uid:that.user.uid,
										withdrawalsimg:'https://api.laotiele.com:9527/image/'+that.upData
									},
							    success: (res) => {
									console.log(res)
									if(res.statusCode==200){
										uni.showToast({
											title:"已发起提现，客服会在24小时处理",
											icon:"none"
										})
										that.wx=false;
										that.flag=false
									}else{
										uni.showToast({
											title:"您的网络打了个盹",
											icon:"none"
										})
									}
							    },
								fail : function(err){
									uni.showToast({
										title:"您的网络打了个盹",
										icon:"none"
									})
								}
							});
							
							
						}else{
							uni.showToast({
								title:"请正确输入提现金额",
								icon:"none"
							})
						}
				    },
					fail : function(err){
						uni.showToast({
							title:"您的网络打了个盹",
							icon:"none"
						})
					}
				});
				
				
				
			},
			wxCJ:function(){
				this.wx=true
			},
			back : function(){
			   uni.navigateBack({
			  
			   });
			},
			tjewm:function(){
				let that=this;
				uni.chooseImage({
				   count: 1, //默认9
				   sizeType: ['compressed'], //可以指定是原图还是压缩图，默认二者都有
				   sourceType: ['album'], //从相册选择
				   success: function (res) {
						console.log(res.tempFilePaths,res);
						let thatRes=res;
						if(/(.jpg|.JPG|.jpeg|.JPEG|.PNG|.png)$/.exec(res.tempFilePaths[0])){
							uni.showLoading({
								title:'正在上传中..'
							})
							let uploadTask=uni.uploadFile({
						    url: that.url1+'/dinghai/Img/Img', //仅为示例，非真实的接口地址
							filePath:thatRes.tempFilePaths[0],
						    name: 'files',
							fileType:"image",
						    success: (uploadFileRes) => {
						        if(uploadFileRes.statusCode===200){
									that.img=thatRes.tempFilePaths[0];
									that.upData=uploadFileRes.data
									uni.showToast({
										title: '上传成功'
									});
								}else{
									uni.showToast({
										title: '上传图片失败请重试',
										icon:"none"
									});
								}
							}
							});
							uploadTask.onProgressUpdate((res) => {
								console.log('上传进度' + res.progress);
								console.log('已经上传的数据长度' + res.totalBytesSent);
								console.log('预期需要上传的数据总长度' + res.totalBytesExpectedToSend);
							});
						}else{
							uni.showToast({
							icon:"none",
							title:"上传格式错误！请上传jpg，jpeg，png格式的图片"
							})
						}
				   }
				});
			}
		 }
}
